import Vue from 'vue';

Vue.directive('scroll-effect', {
    inserted(el) {
        let isVisible = false;

        window.addEventListener('scroll', handleScroll);

        function handleScroll() {
            const scrollTop = document.documentElement.scrollTop;
            const elementTop = el.offsetTop;

            if (elementTop <= scrollTop + window.innerHeight * 0.7) {
                if (!isVisible) {
                    el.style.opacity = 1;
                    isVisible = true;
                }
            } else {
                if (isVisible) {
                    el.style.opacity = 0;
                    isVisible = false;
                }
            }
        }
    }
});