<script>
export default {
  name: 'FootBox',
  data() {
    return {
      visitCount: 1  // 初始化访问次数为1
    }
  },
  mounted() {
    this.visitCount = this.getVisitCount(); // 获取本地存储中的访问次数
    this.saveVisitCount(this.visitCount + 1); // 访问次数加1并保存回本地存储
  },
  methods: {
    getVisitCount() {
      const count = localStorage.getItem('visitCount'); // 从本地存储中获取访问次数
      return count ? parseInt(count, 10) : 0; // 若本地存储中没有访问次数，则返回0
    },
    saveVisitCount(count) {
      localStorage.setItem('visitCount', count); // 将访问次数保存到本地存储
    }
  }
}
</script>

<template>
  <div id="foot-box">
    <hr>
    <div id="foot-text">
      Copyright © 2023 渣豪. 保留所有权利。
      <span id="tagText-foot">热烈欢迎<span style="color: red;margin:0 5px ">{{
          visitCount
        }}</span>号大爷莅临本站</span>
    </div>
  </div>
</template>

