<script>

import FootBox from "@/components/footer/FooterBox.vue";
import HeaderBox from "@/components/header/HeaderText.vue";
import ContentBox from "@/components/content/ContentBox.vue";
import HeadImg from "@/components/header/HeaderImg.vue";
import '@/styles/content/content.scss'

export default {
  name: 'IndexPage',
  components: {
    HeaderBox,
    HeadImg,
    ContentBox,
    FootBox
  },
  data() {
    return {
      startTime: 0, // 用户进入网站的时间戳
      currentTime: 0 // 当前时间
    }
  },
  computed: {
    formatTime() {
      const hours = String(Math.floor(this.currentTime / 3600)).padStart(2, '0');
      const minutes = String(Math.floor((this.currentTime % 3600) / 60)).padStart(2, '0');
      const seconds = String(Math.floor(this.currentTime % 60)).padStart(2, '0');
      return `${hours}:${minutes}:${seconds}`;
    }
  },
  mounted() {
    this.startTime = Math.floor(Date.now() / 1000); // 记录用户进入网站的时间戳
    setInterval(() => {
      const now = Math.floor(Date.now() / 1000); // 获取当前时间戳
      this.currentTime = now - this.startTime; // 计算已经过去的时间
    }, 1000);
  }
}

</script>

<template>
  <div id="index">
    <HeaderBox></HeaderBox>
    <div id="head-intro">
      渣豪
      <span class="tagText">与你相遇：{{ formatTime }}</span>
    </div>
    <HeadImg></HeadImg>
    <ContentBox></ContentBox>
    <FootBox></FootBox>
  </div>
</template>

